import React from "react";
import { Modal, ModalHeader, ModalBody, ModalFooter, Button } from "reactstrap";

// create a fullscreen modal
export default function ({ data, showModal, toggleModal }) {
  return (
    <Modal isOpen={showModal} size="lg">
      <ModalHeader className="flex justify-between">
        {data?.word} &nbsp; /{data?.ipa}/
      </ModalHeader>
      <ModalBody>
        <div className="flex flex-col">
          <audio controls className="block float-right">
            <source
              src={`http://localhost:3001/audio/${data?.word}`}
              type="audio/mpeg"
            />
          </audio>
          <div className="flex flex-col flex-wrap">
            {data?.definitions?.map((item, index) => {
              return (
                <div
                  key={index}
                  className="flex flex-col bg-blue-50 px-3 py-2 m-2"
                >
                  {item.definition}
                  {item.examples.map((example, index) => {
                    return (
                      <div key={index} className="flex flex-row">
                        <span className="text-sm text-gray-600">{example}</span>
                      </div>
                    );
                  })}
                </div>
              );
            })}
          </div>
        </div>
      </ModalBody>
      <ModalFooter>
        <Button onClick={toggleModal.bind(this, false)}>Close</Button>
      </ModalFooter>
    </Modal>
  );
}
